import React from 'react';
import { useAuth } from '../../contexts/AuthContext';
import Button from '../../components/Button';
import { CreditCard, Calendar, ShieldCheck } from 'lucide-react';

const BillingPage = () => {
  const { gym } = useAuth();

  return (
    <div className="space-y-6">
      <h2 className="text-2xl font-bold text-slate-100">Facturacin y Suscripcin</h2>

      <div className="grid gap-6 lg:grid-cols-2">
        {/* Current Plan */}
        <div className="rounded-xl border border-slate-800 bg-slate-900 p-6">
          <h3 className="mb-4 text-lg font-semibold text-slate-100">Plan Actual</h3>
          <div className="flex items-start justify-between">
            <div>
              <p className="text-3xl font-bold text-lime-400 uppercase">{gym?.plan_type}</p>
              <p className="mt-1 text-slate-400">Renovacin automtica el 18 de Enero, 2026</p>
            </div>
            <div className="rounded-full bg-green-400/10 px-3 py-1 text-xs font-medium text-green-400 border border-green-400/20">
              ACTIVO
            </div>
          </div>
          
          <div className="mt-6 flex gap-3">
            <div className="group relative">
              <Button variant="outline" disabled>
                Cambiar Plan
              </Button>
              <div className="absolute bottom-full left-1/2 mb-2 hidden -translate-x-1/2 rounded bg-slate-800 px-2 py-1 text-xs text-white group-hover:block whitespace-nowrap">
                Prximamente
              </div>
            </div>
            <Button variant="secondary">Cancelar Suscripcin</Button>
          </div>
        </div>

        {/* Payment Method */}
        <div className="rounded-xl border border-slate-800 bg-slate-900 p-6">
          <h3 className="mb-4 text-lg font-semibold text-slate-100">Mtodo de Pago</h3>
          <div className="flex items-center gap-4 rounded-lg border border-slate-800 bg-slate-950 p-4">
            <div className="rounded-lg bg-slate-800 p-2">
              <CreditCard className="h-6 w-6 text-slate-300" />
            </div>
            <div>
              <p className="font-medium text-slate-200">Visa terminada en 4242</p>
              <p className="text-sm text-slate-500">Expira 12/28</p>
            </div>
          </div>
          <Button variant="outline" className="mt-4 w-full">
            Actualizar Mtodo de Pago
          </Button>
        </div>
      </div>

      {/* Billing History */}
      <div className="rounded-xl border border-slate-800 bg-slate-900 p-6">
        <h3 className="mb-4 text-lg font-semibold text-slate-100">Historial de Pagos</h3>
        <div className="rounded-lg border border-slate-800 bg-slate-950 p-8 text-center">
          <Calendar className="mx-auto mb-3 h-10 w-10 text-slate-600" />
          <p className="text-slate-400">No hay facturas anteriores para mostrar.</p>
        </div>
      </div>
    </div>
  );
};

export default BillingPage;