import React from 'react';
import { User, Building2, Lock } from 'lucide-react';

const SettingsPage = () => {
  return (
    <div className="space-y-6">
      <h2 className="text-2xl font-bold text-slate-100">Configuracin</h2>

      <div className="rounded-xl border border-slate-800 bg-slate-900">
        <div className="border-b border-slate-800">
          <nav className="flex gap-4 px-6" aria-label="Tabs">
            <button className="border-b-2 border-lime-400 px-1 py-4 text-sm font-medium text-lime-400">
              Perfil
            </button>
            <button className="border-b-2 border-transparent px-1 py-4 text-sm font-medium text-slate-400 hover:border-slate-300 hover:text-slate-300">
              Gimnasio
            </button>
            <button className="border-b-2 border-transparent px-1 py-4 text-sm font-medium text-slate-400 hover:border-slate-300 hover:text-slate-300">
              Seguridad
            </button>
          </nav>
        </div>

        <div className="p-6">
          <div className="flex flex-col items-center justify-center py-12 text-center">
            <div className="mb-4 rounded-full bg-slate-800 p-4">
              <Building2 className="h-8 w-8 text-slate-400" />
            </div>
            <h3 className="text-lg font-medium text-slate-100">En Desarrollo</h3>
            <p className="mt-2 max-w-sm text-slate-400">
              Estamos trabajando duro para traerte estas funcionalidades. Pronto podrs configurar cada detalle de tu gimnasio!
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingsPage;